@import url(./avenir.css);
@import url(./fontello.css);
.about-section {
  text-align: left;
  color: #bbb; }
  .about-section .about-section-header {
    color: #fff;
    padding-bottom: 10px; }

.about hr {
  margin: 7vh; }

.carousel {
  margin-top: 16px; }

.carousel-caption {
  background-color: rgba(0, 0, 0, 0.4); }

.carousel-item-link p {
  text-decoration: underline; }

.carousel-control-button {
  height: 30px;
  width: 26px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.4); }

.indicators-top {
  position: relative;
  margin-top: 16px; }

.navbar {
  padding: 10px;
  padding-left: 10vh; }

@media (min-width: 992px) {
  .navbar-expand-lg .navbar-nav .nav-link {
    padding-right: 0.5rem;
    padding-left: 0.5rem; } }

.work-header {
  color: white; }

.pdf-object {
  min-height: 500px; }

.projects-div {
  margin-bottom: 50px; }

.resume-subheader {
  color: #bbb; }

footer {
  border-top: 1px solid #777777; }

.footer-head {
  color: #fff; }

.footer-link {
  color: #bbb; }

.footer-text {
  color: #777777; }

body {
  font-family: Avenir Next;
  text-align: center;
  background-color: rgba(52, 58, 64, 0.95); }

.componentsDiv {
  padding: 10vh; }

a {
  color: #f5a9b8; }

hr {
  border-color: rgba(245, 169, 184, 0.7); }

.home-header {
  font-size: 3rem;
  margin: 1rem; }
  .home-header.home-header-1 {
    font-size: 5rem;
    color: #f5a9b8; }
  .home-header.home-header-2 {
    color: #ddd; }
  .home-header.home-header-3 {
    color: #bbb; }
  .home-header.home-header-4 {
    color: #999; }
  .home-header.home-header-5 {
    color: #424242; }
    .home-header.home-header-5:hover {
      color: rgba(245, 169, 184, 0.3); }

.greyscale-3 {
  color: rgba(255, 255, 255, 0.5); }



